body {
  margin: 0;
  padding: 0;
}
body #image1 {
  display: table;
  width: 100%;
  height: 50%;
  background: url("https://images.unsplash.com/photo-1461016951828-c09537329b3a?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&s=7777e79b0b2c539d64f1f356d09ed136");
  background-size: cover;
  background-position: center center;
}
.image .wrapper {
  margin: 0;
  padding: 0;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  width: 100%;
  position: absolute;
  top: 15%;
}
.image .wrapper h1 {
  font-family: 'PT Serif', serif;
  font-size: 5em;
  font-weight: 600;
  color: #fff;
}
.image .wrapper hr {
  max-width: 500px;
  max-height: 50px;
  border: 0;
  height: 3px;
  background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
  background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
}
.image .wrapper h3 {
  color: #fff;
  font-family: 'Merriweather', serif;
  font-size: 2em;
}
.services .content .wrapper {
  min-height: 50px;
}
.services .content .wrapper h1 {
  padding-top: 30px;
  width: 100%;
  text-align: center;
  font-size: 3em;
  font-weight: 400;
  font-family: 'Lobster', cursive;
}
.services .content .wrapper hr {
  margin-top: -5px;
  max-width: 300px;
  padding: 0;
  border: none;
  border-top: medium double #333;
  color: #333;
  text-align: center;
}
.services .content .wrapper hr:after {
  content: "✻";
  display: inline-block;
  position: relative;
  top: -0.7em;
  font-size: 1.5em;
  padding: 0 0.25em;
  background: #fff;
}
.services .content .wrapper hr {
  padding-bottom: 20px;
}
.services .content .service-wrapper {
  margin-top: 20px;
  padding-left: 330px;
  padding-right: 330px;
  height: 250px;
}
.services .content .service-wrapper .service {
  width: 100px;
  height: 200px;
  width: calc(99.99% * 1/3 - (30px - 30px * 1/3));
  margin-bottom: 20px;
  text-align: center;
}
.services .content .service-wrapper .service:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none;
}
.services .content .service-wrapper .service:last-child {
  margin-right: 0;
}
.services .content .service-wrapper .service:nth-child(3n) {
  margin-right: 0;
  float: right;
}
.services .content .service-wrapper .service:nth-child(3n + 1) {
  clear: left;
}
@media only screen and (max-width: 400px) {
  .services .content .service-wrapper .service {
    width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
  }
  .services .content .service-wrapper .service:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none;
  }
  .services .content .service-wrapper .service:last-child {
    margin-right: 0;
  }
  .services .content .service-wrapper .service:nth-child(1n) {
    margin-right: 0;
    float: right;
  }
  .services .content .service-wrapper .service:nth-child(1n + 1) {
    clear: left;
  }
}
.services .content .service-wrapper .service h3 {
  font-family: 'Merriweather', serif;
  text-align: center;
  width: 100%;
  font-size: 2em;
}
.services .content .service-wrapper .service p {
  font-family: 'Lato', sans-serif;
  color: #808080;
}
.services .content .service-wrapper .service i {
  font-size: 60px;
  color: #c5a47e;
  margin-bottom: 10px;
}
